* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
.bodya {
  background: url("https://3.bp.blogspot.com/-SdQd09AVXWI/XD9CKyfKzfI/AAAAAAAAArI/Y-GHkDB5lBkYdejbiy2UZSuoLQRFYMoJACKgBGAs/w2560-h1440-c/anime-girl-landscape-shrine-gate-scenery-27-4k.jpg")
  no-repeat;
  background-size: cover;
  position: fixed;
  font-family: sans-serif;
}




  .login-wrapper {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .form {
    position: relative;
    width: 100%;
    margin-right: 20px;
    max-width: 380px;
    padding: 80px 40px 40px;
    background: rgba(0, 0, 0, 0.7);
    border-radius: 10px;
    color: #fff;
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
  }
  
  .form::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    background: rgba(255, 255, 255, 0.08);
    transform: skewX(-26deg);
    transform-origin: bottom left;
    border-radius: 10px;
    pointer-events: none;
  }
  
  .form h2 {
    text-align: center;
    letter-spacing: 1px;
    margin-bottom: 2rem;
    color: #ff652f;
  }
  
  .form .input-group {
    position: relative;
  }
  
  .form .input-group input {
    width: 100%;
    padding: 10px 0;
    font-size: 1rem;
    letter-spacing: 1px;
    margin-bottom: 30px;
    border: none;
    border-bottom: 1px solid #fff;
    outline: none;
    background-color: transparent;
    color: inherit;
  }
  
  .form .input-group label {
    position: absolute;
    top: 0;
    left: 0;
    padding: 10px 0;
    font-size: 1rem;
    pointer-events: none;
    transition: 0.3s ease-out;
  }
  
  .form .input-group input:focus + label,
  .form .input-group input:valid + label {
    transform: translateY(-18px);
    color: #ff652f;
    font-size: .8rem;
  }
  
  .submit-btn {
    display: block;
    margin-left: auto;
    border: none;
    outline: none;
    background: #ff652f;
    font-size: 1rem;
    text-transform: uppercase;
    letter-spacing: 1px;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .forgot-pw {
    color: inherit;
  }
  
  #forgot-pw {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    right: 0;
    height: 0;
    z-index: 1;
    background: #fff;
    opacity: 0;
    transition: 0.6s;
  }
  
  #forgot-pw:target {
    height: 100%;
    opacity: 1;
  }
  
  .close {
    position: absolute;
    right: 1.5rem;
    top: 0.5rem;
    font-size: 2rem;
    font-weight: 900;
    text-decoration: none;
    color: inherit;
  }
  
  @media only screen and (max-width: 600px) {
    .bodya {
      background:url("https://wallpapercave.com/wp/wp5115027.jpg") no-repeat;
      background-size: cover;
    position: fixed;    
  
    }

    .submit-btn{
      background-color: pink;
    }
    
    .form h2{
      color:pink ;
    }

  }